import { Checkbox, Divider, Button,Input, Space, message, Tooltip } from "antd";
import { useState } from "react";
const { TextArea } = Input;
import { ArrowDownOutlined, ArrowUpOutlined } from '@ant-design/icons';
import { copyTextToClipboard } from "./../../lib"
import { openFile } from "../../lib/file"
import { Base64 as B64 } from 'js-base64';
import { default as Base64Intro } from "./intro"

const Base64 = () => {

  const tips = `
Base64"+/=""/""="URLURLBase64%XXURL
URLBase64 URLBase64

  1 3=
  2 Base64"+""/"URLURL"+""/""-""_"

Base64
`;

  const [ encodeValue, setEncodeValue ] = useState('');
  const [ decodeValue, setDecodeValue ] = useState('');
  const [ safe, setSafe ] = useState(false);
  const [ notice, contextHolder ] = message.useMessage(); // 

  const textareaDoubleClick = (e :React.MouseEvent<HTMLTextAreaElement>) => {
    const txt = (e.target as HTMLInputElement).value.trim();
    if(txt !== '') {
      copyTextToClipboard(txt);
      notice.success("");
    }
  };

  const encode = () => {
    if (encodeValue.trim() != "") {
      setDecodeValue( B64.encode(encodeValue, safe) );
    }
  }

  const decode = () => {
    if (decodeValue.trim() != "") {
      let r = ""
      try {
        r = B64.decode( decodeValue)
      } catch(err) {
        notice.error("");
      }
      setEncodeValue(r);
    }
  }

  return (
    <div>
      {contextHolder}

      <TextArea
        style={ { margin: "5px 0 5px 0" }}
        onDoubleClick={ textareaDoubleClick }
        onChange={ (e) => { setEncodeValue(e.target.value) ;} }
        title=""
        value= { encodeValue }
        placeholder=" Base64    "
        autoSize={{ minRows: 5, maxRows: 5 }}
        onDragOver={ (e) => { e.preventDefault(); } } // 
        onDrop={ (e) => { e.preventDefault(); openFile(e.dataTransfer.files, setEncodeValue ); } }
      />

      <Button 
        onClick={ encode }
        style={ {"backgroundColor" : "#007bff","color": "#fff" }} 
        icon={<ArrowDownOutlined />}
      >Base64 </Button>
      <Button 
        onClick={ decode }
        style={ {"backgroundColor" : "#28a745","color": "#fff" }} 
        icon={<ArrowUpOutlined />}
      >Base64 </Button>&nbsp;
      <Tooltip placement="bottomRight" title={ tips }>
        <Checkbox onChange={ (e) => { setSafe(e.target.checked); } } checked={ safe }></Checkbox>
      </Tooltip>&nbsp;
      <Button 
        onClick={ () => { setEncodeValue(''); setDecodeValue(''); } }
        style={ {"backgroundColor" : "#dc3545","color": "#fff" }} 
      ></Button>
      
      <TextArea
        style={ { margin: "5px 0 5px 0" }}
        onDoubleClick={ textareaDoubleClick }
        onChange={ (e) => { setDecodeValue(e.target.value) ;} }
        title=""
        value= { decodeValue }
        placeholder=" Base64    "
        autoSize={{ minRows: 5, maxRows: 5 }}
        onDragOver={ (e) => { e.preventDefault(); } } // 
        onDrop={ (e) => { e.preventDefault(); openFile(e.dataTransfer.files, setDecodeValue ); } }
      />
      
      <Divider> Base64  </Divider>

      <Base64Intro />
    </div>
  );
}

export default Base64;